.dashboard {
  padding: 20px;
}

.dashboard-layout {
  display: grid;
  grid-template-columns: 250px 1fr;
  gap: 20px;
  max-width: 1400px;
  margin: 0 auto;
}

.main-content {
  min-height: calc(100vh - 100px);
}

.dashboard-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;
}

.dashboard-header h1 {
  color: #333;
  margin: 0;
}

.controls {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.category-filters {
  display: flex;
  gap: 10px;
}

.category-btn {
  padding: 8px 16px;
  border: none;
  border-radius: 4px;
  background: #fff;
  cursor: pointer;
  transition: all 0.3s ease;
}

.category-btn.active {
  background: #1976d2;
  color: white;
}

.language-select {
  padding: 8px 12px;
  border: 1px solid #ddd;
  border-radius: 4px;
  background: white;
  cursor: pointer;
}

.loader {
  text-align: center;
  padding: 40px;
  color: #666;
  font-size: 1.2em;
}

/* Responsive Design */
@media (max-width: 768px) {
  .dashboard-layout {
    grid-template-columns: 1fr;
  }
}

/* Dark Mode Support */
@media (prefers-color-scheme: dark) {
  .dashboard-header h1 {
    color: #e0e0e0;
  }

  .language-select {
    background: #333;
    color: #e0e0e0;
    border-color: #444;
  }

  .loader {
    color: #ccc;
  }
} 